@import url('https://fonts.googleapis.com/css2?family=Encode+Sans+SC:wght@500&display=swap');
* {
	padding: 0;
	margin: 0;
	border: 0;
}
*,
*:before,
*:after {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,
:active {
	outline: none;
}
a:focus,
a:active {
	outline: none;
}
nav,
footer,
header,
aside {
	display: block;
}
html,
body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
	font-family: inherit;
}
input::-ms-clear {
	display: none;
}
button {
	cursor: pointer;
}
button::-moz-focus-inner {
	padding: 0;
	border: 0;
}
a,
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}
img {
	vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: inherit;
}
::-moz-selection {
	background: #6576b4;
}
::selection {
	background: #6576b4;
}
body {
	background-color: #4158d0;
	background-image: linear-gradient(
		43deg,
		#4158d0 0%,
		#70ffff 20%,
		#c850c0 46%,
		#4158d0 67%,
		#c850c0 100%
	);
	background-size: 400% 400%;
	font-family: 'Encode Sans SC', sans-serif;
	height: 100vh;
	animation: bg 25s ease-in infinite alternate-reverse;
}
@keyframes bg {
	0%,
	100% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100%, 50%;
	}
}
body .greeting {
	text-align: center;
	font-size: 2.5em;
	width: 300px;
	padding-top: 15px;
	margin: 0 auto;
	color: rgb(245, 244, 244);
}
.container {
	background: rgba(255, 255, 255, 0.05);
	border-radius: 6px;
	backdrop-filter: blur(15px);
	border-top: 1px solid rgba(255, 255, 255, 0.2);
	border-left: 1px solid rgba(255, 255, 255, 0.2);
	box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
	width: 300px;
	display: block;
	margin: 0 auto;
	margin-top: 50px;
	border-radius: 10px;
	padding-bottom: 50px;
	-webkit-transition: -webkit-box-shadow 0.3s ease-in;
	transition: -webkit-box-shadow 0.3s ease-in;
	transition: box-shadow 0.3s ease-in;
	transition: box-shadow 0.3s ease-in, -webkit-box-shadow 0.3s ease-in;
}
.container:hover {
	-webkit-box-shadow: 0px 10px 30px 0px #293251;
	box-shadow: 0px 10px 30px 0px #293251;
}
.container .app-title {
	width: 300px;
	height: 50px;
	border-radius: 10px 10px 0 0;
}
.container .app-title p {
	text-transform: uppercase;
	text-align: center;
	padding: 15px;
	margin: 0 auto;
	font-size: 1.6em;
	color: rgb(245, 244, 244);
}
.container .notification {
	background-color: #f8d7da;
}
.container .notification p {
	color: #721c24;
	font-size: 1.2em;
	text-align: center;
	padding: 10px 0;
}
.weather-container {
	width: 300px;
	padding: 0 0 20px 0;
}
.weather-container__icon {
	width: 300px;
	height: 128px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	pointer-events: none;
}
.weather-container__icon img {
	display: block;
	margin: 0 auto;
}
.weather-container__temperature-value {
	width: 300px;
	height: 60px;
}
.weather-container__temperature-value p {
	color: rgb(245, 244, 244);
	font-size: 4em;
	text-align: center;
}
.weather-container__temperature-value span {
	color: rgb(245, 244, 244);
	font-size: 0.8em;
}
.weather-container__feels-value {
	width: 300px;
	height: 30px;
}
.weather-container__feels-value p {
	color: rgb(245, 244, 244);
	font-size: 1em;
	text-align: center;
}
.weather-container__temperature-descripton p {
	padding: 8px;
	color: rgb(245, 244, 244);
	text-align: center;
	font-size: 1.4em;
}
.weather-container__location p {
	color: rgb(245, 244, 244);
	text-align: center;
	font-size: 1.1em;
}
